%extend_hamburger_color {
   .hamburger-inner,
   .hamburger-inner::before,
   .hamburger-inner::after {
      background-color: $navbar-light-bg;
   }
}
@mixin fullNav {
   .navbar-header {
      width: 100% !important;
      padding: 0.5rem 1rem;
      position: relative;

      .menu-toggle {
         top: 2px;
         position: relative;
      }

      .open-navbar-container i {
         font-size: 1.8rem;
      }

      .navbar-brand {
         position: absolute;
         left: 50%;
         top: 0;
         transform: translate(-50%, 0);
      }
   }

   &.navbar-with-menu {
      .navbar-container {
         width: 100%;
         display: table;
         background: #fff;
         padding: 0rem 1rem;
         margin: 0;
         height: auto;
      }
   }
}

.header-navbar {
   padding: 0px;
   min-height: $navbar-height;
   font-family: $font-family-monospace;
   transition: 300ms ease all;
   &.navbar-static-top{
      z-index: 1038;
   }
   .navbar-wrapper {
      width: 100%;
   }

   // add border to navbar
   &.navbar-border {
      border-bottom: 1px solid #e4e7ed;
   }

   &.navbar-dark.navbar-border {
      border-bottom: 1px solid #8596b5;
   }

   // add shadow to navbar
   &.navbar-shadow {
      box-shadow: 0px 2px 30px 2px rgba(0, 0, 0, 0.1);
   }

   // hide top horizontal menu drop-down arrow
   &.navbar-without-dd-arrow {
      .nav-link {
         &.dropdown-toggle::after {
            display: none;
         }
      }
   }

   //center align brand option for horizontal layouts
   &.navbar-brand-center {
      .navbar-header {
         width: 192px;
         position: absolute;
         top: 0;
         left: 50%;
         padding: 0;
         margin: 0;
         transform: translate(-50%, 0);
         z-index: 999;
      }
   }

   .navbar-header {
      height: 100%;
      width: $navbar-header-width;
      height: $navbar-height + 0.1;
      float: left;
      position: relative;
      padding: 0rem 0.85rem;
      transition: 300ms ease all;

      .navbar-brand {
         padding: 18px 0px;
         margin-right: 0;

         .brand-logo {
            width: 36px;
         }

         .brand-text {
            display: inline;
            padding-left: 6px;
            font-weight: 500;
            vertical-align: middle;
         }
      }
   }

   .navbar-container {
      padding: 0rem 18px;
      height: $navbar-height;
      transition: 300ms ease all;
      background: inherit;

      ul.nav {
         li {
            > a.nav-link {
               padding: 1.9rem 1rem;
               font-size: 1.15rem;

               i.flag-icon {
                  margin-right: 0.5rem;
               }
            }

            a.dropdown-user-link {
               padding: 1.2rem 1rem;
               line-height: 23px;

               .user-name {
                  display: inline-block;
                  margin-bottom: 0.4rem;
                  margin-left: 0.2rem;
               }
            }

            a.menu-toggle {
               padding: 1.7rem 1rem 1.6rem 1rem;

               i {
                  font-size: 1.6rem;
               }
            }

            a.nav-link-label {
               padding: 1.7rem 1rem 1.6rem 1rem;
               height: 100%;
            }

            a.dropdown-user-link {
               .avatar {
                  margin-right: 0.5rem;
                  width: 36px;
               }
            }

            a.nav-link-search,
            a.nav-link-expand {
               padding: 1.7rem 1rem 1.7rem 1rem;
            }

            div.input-group {
               padding: 0.7rem 1rem;
            }

            i.ficon {
               font-size: 1.5rem;
            }

            .media-list {
               max-height: 18.2rem;
               top: -20px;
            }

            .scrollable-container {
               position: relative;
            }
         }
      }

      //Navbar notification css
      .dropdown-menu-media {
         width: 24rem;

         .media-list {
            .media {
               padding: 1rem;
               border: none;
               // border-top: 1px solid $border-color;
               border-bottom: 1px solid $border-color;
            }
         }

         .dropdown-menu-header {
            border-bottom: 1px solid $border-color;
         }

         .dropdown-menu-footer {
            position: relative;
            top: -20px;
            a {
               padding: 0.3rem;
               border-top: 1px solid $border-color;
               margin-bottom: -20px;
            }
         }
      }
   }

   &.navbar-without-menu {
      .navbar-container {
         margin-left: 0;
      }
   }

   &.navbar-with-menu {
      .navbar-container {
      }
   }

   .nav-item + .nav-item {
      margin-left: 0rem;
   }

   /* For sm screen*/
   @include media-breakpoint-down(sm) {
      @include fullNav;
   }
}
/* Modern menu For md screen*/
@include media-breakpoint-down(md) {
   [data-menu="vertical-menu-modern"] {
      .header-navbar {
         @include fullNav;
      }
      .navbar-dark,
      .navbar-semi-dark {
         .navbar-header .navbar-nav .nav-link {
            color: $white;
         }
         .navbar-container .navbar-nav .nav-link {
            color: $navbar-dark-bg;
         }
      }
      .navbar-light {
         .navbar-header .navbar-nav .nav-link {
            color: $navbar-dark-bg;
         }
      }
      .navbar-header .navbar-nav {
         .open-navbar-container,
         .nav-menu-main {
            padding: 1rem;
         }
      }
   }
}

