// Mega menu
// ------------------------------
.mega-dropdown {
   position: static !important;

   .dropdown-menu {
      display: flex;
   }
}

.navigation-main,
.navbar-nav {
   .mega-dropdown-menu {
      font-family: $font-family-sans-serif;
      width: calc(100% - 50px);
      padding: 0.5rem 0;
      // width: 98%;
      /* fallback if needed */
      margin: 0px 25px;
      box-shadow: none;
      box-shadow: 0px 5px 75px 2px rgba(64, 70, 74, 0.2);
      z-index: 9999;

      ul {
         padding: 0;
         margin: 0;

         li {
            list-style: none;

            a {
               display: block;
               color: $gray-800;
               &.dropdown-item{
                  &:active{
                     color: $white;
                  }
               }

               .children-in {
                  float: right;
               }

               &:hover {
                  text-decoration: none;
                  color: $gray-600;
               }

               &:focus {
                  text-decoration: none;
                  color: $gray-600;
               }
            }
         }

         a.dropdown-item {
            padding: 10px;
         }
      }

      p.accordion-text {
         line-height: $line-height-base;
      }

      .dropdown-menu-header {
         padding: 0.5rem 0;
         border-bottom: 1px solid $border-color;
      }

      h6 i.la {
         font-size: 1.1rem;
      }
   }
}

// TODO: Need to generalize it
.horizontal-top-icon-menu {
   .mega-dropdown-menu {
      margin: 0px 25px !important;
   }
}

// Mega menu max width on boxed layout only
.container {
   .mega-dropdown-menu {
      @include mega-menu-container-max-widths();
   }
}

@-moz-document url-prefix() {
   ul {
      li {
         a {
            .children-in {
               position: relative;
               right: 3px;
               top: -14px;
            }
         }
      }
   }
}

// Mega menu list
// ------------------------------
.menu-list {
   list-style: none;
   padding: 0;
   overflow: hidden;
}

.menu-panel {
   padding: 0;
}

.sliding-menu {
   overflow: hidden;
   position: relative;

   ul {
      float: left;
      margin: 0;
      padding: 0;

      li {
         list-style: none;
         margin: 0;
      }
   }
}

// Mega "Hamburger" Icon Animations
// ------------------------------
.navbar-menu-icon {
   width: 24px;
   margin: 16px 2px !important;
   position: relative;
   margin: 0px auto;
   transform: rotate(0deg);
   transition: 0.5s ease-in-out;
   cursor: pointer;
}

.navbar-menu-icon {
   span {
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      background: $gray-600;
      border-radius: 9px;
      opacity: 1;
      left: 0;
      transform: rotate(0deg);
      transition: 0.25s ease-in-out;
   }
}

.navbar-menu-icon span:nth-child(1) {
   top: 0px;
}

.navbar-menu-icon span:nth-child(2) {
   top: 10px;
}

.navbar-menu-icon span:nth-child(3) {
   top: 20px;
}

.navbar-menu-icon.show span:nth-child(1) {
   top: 10px;
   transform: rotate(135deg);
}

.navbar-menu-icon.show span:nth-child(2) {
   opacity: 0;
   left: -60px;
}

.navbar-menu-icon.show span:nth-child(3) {
   top: 10px;
   transform: rotate(-135deg);
}

// Media queries for device support
// =========================================
@include media-breakpoint-down(sm) {
   .header-navbar {
      // search bar
      #navbar-mobile {
         .search-input.open {
            .input {
               width: 132px;
               &::placeholder {
                  color: $input-placeholder-color;
               }
            }
         }
      }

      // generic navbar dropdown specific
      .navbar-nav {
         .show {
            position: static;
         }

         .open-navbar-container {
            padding-top: 0.9rem;
         }
      }

      .navbar-container {
         .show {
            .dropdown-menu {
               right: 0;
               left: 0 !important;
               float: none;
               width: auto;
               margin-top: 0;
               max-height: 400px;
               overflow-x: hidden;
               overflow-y: hidden; //prev. was scroll
            }
         }

         ul.nav {
            li {
               &.nav-item i {
                  margin-right: 0.2rem;
               }

               > a.nav-link {
                  padding: 1.5rem 0.4rem;

                  i.flag-icon {
                     margin-right: 0.2rem;
                  }
               }

               a.dropdown-user-link {
                  padding: 0.8rem 0.6rem !important;
               }

               a.nav-link-label {
                  padding: 1.4rem 0.4rem;
                  i.ft-bell {
                     position: relative;
                     left: 8px;
                  }
               }

               a.nav-link-search,
               a.nav-link-expand {
                  padding: 1.4rem 1rem 1rem 1rem;
               }

               .dropdown-toggle::after {
                  margin-right: 0;
                  margin-left: -2px;
               }
            }
         }
      }
   }

   // dark navbar
   .navbar-dark,
   .navbar-semi-dark {
      .hamburger-inner,
      .hamburger-inner::before,
      .hamburger-inner::after {
         background-color: $navbar-light-bg;
      }

      .navbar-header {
         .navbar-nav {
            .nav-link {
               color: $navbar-light-bg;
            }
         }
      }

      .navbar-container {
         .navbar-nav {
            .nav-link {
               color: $gray-600;
            }
         }
      }
   }

   // light navbar
   .navbar-light,
   .navbar-semi-light {
      .navbar-header {
         .navbar-nav {
            .nav-link {
               color: $navbar-dark-bg;
            }
         }
      }

      .navbar-container {
         .navbar-nav {
            .nav-link {
               color: $gray-600;
            }
         }
      }
   }
}

.headroom {
   will-change: transform;
   transition: transform 200ms linear;
}

.headroom--pinned-top {
   transform: translateY(0%);
}

.headroom--unpinned-top {
   transform: translateY(-100%);
}

.headroom--pinned-bottom {
   transform: translateY(0%);
}

.headroom--unpinned-bottom {
   transform: translateY(100%);
}

// iPad support for navbr
@include media-breakpoint-down(lg) {
   .header-navbar {
      .navbar-container {
         ul.nav {
            li {
               > a.nav-link {
                  // padding: 1.4rem 0.4rem 0.8rem 0.4rem;
                  i.flag-icon {
                     margin-right: 0.2rem;
                  }
               }

               a.dropdown-user-link {
                  // padding: 0.8rem 0.6rem;
                  padding: 1.2rem 0.6rem;
               }

               a.nav-link-label {
                  // padding: 1.2rem 0rem 1rem 0.8rem;
               }

               a.nav-link-search,
               a.nav-link-expand {
                  // padding: 1.5rem 1rem 1rem 1rem;
               }

               .dropdown-toggle::after {
                  margin-right: 0;
                  margin-left: -2px;
               }

               .selected-language {
                  display: none;
               }

               a.dropdown-user-link {
                  .user-name {
                     display: none;
                  }
               }
            }
         }
      }
   }
}

@mixin extend_mobile_specific($menu_height) {
   .header-navbar {
      .navbar-container {
         .show {
            .dropdown-menu {
               max-height: $menu_height;
            }
         }
      }
   }
}

@mixin responsive_sub_nav {
   //For responsive sub nav
   #navbar-mobile.navbar-collapse {
      .navbar-nav {
         margin: 0;
         flex-flow: row wrap;

         .dropdown-menu {
            position: absolute;
         }

         .nav-item {
            float: left;
         }
      }
   }
}

@media (max-width: 767px) {
   @include responsive_sub_nav;
}
@include media-breakpoint-down(md) {
   @include responsive_sub_nav;
}

/* ----------- iPhone 5, 5S  iPhone 6----------- */

/* Landscape */

@media only screen and (min-device-width: 26.78em) and (max-device-width: 47.64em) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
   @include extend_mobile_specific(180px);
}

/* ----------- iPhone 6+ ----------- */

/* Landscape */

@media only screen and (min-device-width: 29.57em) and (max-device-width: 52.57em) and (-webkit-min-device-pixel-ratio: 3) and (orientation: landscape) {
   @include extend_mobile_specific(280px);
}

